import { getBrands } from "@/api";
import { HomeCrumb } from "@/components/bread-crumb";
import { Container } from "@/components/container";
import { InfoHeader } from "@/components/div";
import { Section } from "@/components/section";
import { InfoTitle } from "@/components/title";
import BrandsClient from "./BrandsClient";

const BrandsPage = async () => {
  const brands = await getBrands();

  // Brendlarni birinchi harfi bo'yicha guruhlash
  const groupedBrands: Record<string, any[]> = {};

  for (const brand of brands) {
    const firstLetter = brand.title?.[0]?.toUpperCase() || "";
    if (!groupedBrands[firstLetter]) {
      groupedBrands[firstLetter] = [];
    }
    groupedBrands[firstLetter].push(brand);
  }

  // Lotin va kirill harflar massivlari
  const latinLetters = "ABCDEFGHIJKLMNOPQRSTUVWXYZ".split("");
  const cyrillicLetters = [
    "А","Б","В","Г","Д","Е","Ж","З","И","Й","К","Л","М",
    "Н","О","П","Р","С","Т","У","Ф","Х","Ц","Ч","Ш","Щ",
    "Э","Ю","Я"
  ];

  return (
    <Container className="pb-[58px]">
      <HomeCrumb paths={[{ name: "Каталог брендов" }]} />
      <Section className="py-6 px-0 shadow-sectionShadow rounded-none">
        <InfoHeader className="mb-5">
          <InfoTitle>Каталог брендов</InfoTitle>
        </InfoHeader>
        <div className="p-6">
          {/* Client Component ga groupedBrands va harflarni uzatamiz */}
          <BrandsClient
            groupedBrands={groupedBrands}
            latinLetters={latinLetters}
            cyrillicLetters={cyrillicLetters}
          />
        </div>
      </Section>
    </Container>
  );
};

export default BrandsPage;
